* {
  margin: 0;
  padding: 0;
  scroll-behavior: smooth;
  outline: none !important;
  box-sizing: border-box;
}

dl, ol, ul {
  margin: 0;
  padding: 0;
}

li {
  list-style: none
}
a, a:hover {
  color: inherit;
  text-decoration: none
}
p {
  margin: 0;
}
img {
  max-width: 100%;
  height: auto;
}
body {
  overflow-x: hidden;
  font-weight: 400;
  font-family: "Gilroy",sans-serif;
}